/* Global Styles - Dark Theme */
body {
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: #E0E0E0;
  margin: 0;
  padding: 0;
}

.App {
  padding: 20px;
}

/* Article Container Styling */
.article-container {
  margin-top: 20px;
  padding: 20px;
  background-color: #1e1e1e; /* Dark background for the article sections */
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  color: #E0E0E0;
  max-width: 800px;
  margin: 20px auto; /* Center the article container */
  line-height: 1.6;
}

/* Main Heading with Gradient */
h2 {
  color: #fff; /* White for titles */
  background: linear-gradient(90deg, #FC466B, #3F5EFB); /* Pink/Purple Gradient */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; /* Apply gradient to text */
  font-size: 2em;
  margin-bottom: 20px;
  text-align: center;
}

/* Subheadings in Plain White */
h3 {
  color: #fff; /* Plain white for subheadings */
  margin-top: 30px;
  font-size: 1.5em;
  border-bottom: 1px solid #333; /* Optional: Subtle underline for subheadings */
  padding-bottom: 10px;
}

/* Paragraph and List Item Styling */
p, ul li {
  color: #B3B3B3; /* Softer gray for paragraph text and list items */
  font-size: 1.1em;
}

/* Unordered List Styling */
ul {
  margin-top: 10px;
  padding-left: 20px;
}

ul li {
  margin-bottom: 10px;
}

/* Section Styling - No Visibility Animations */
.article-container section {
  opacity: 1; /* Ensure sections are fully visible */
  transform: scale(1); /* No transform, fully scaled */
}

/* Button Styles (if needed) */
button {
  background: linear-gradient(45deg, #00C9FF, #92FE9D); /* Gradient button */
  border: none;
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease;
}

/* Button Hover Effect */
button:hover {
  background: linear-gradient(45deg, #0575E6, #00F260); /* Darker gradient on hover */
}

/* ==================== MOBILE OPTIMIZATION ==================== */
@media (max-width: 768px) {
  /* Adjust the padding and margins for smaller screens */
  .App {
    padding: 10px;
  }

  .navbar {
    padding: 15px;
    font-size: 1.2em;
  }

  /* Reduce max-width for smaller screens */
  .article-container {
    padding: 15px;
    max-width: 100%; /* Full width for mobile screens */
    margin: 10px;
  }

  /* Adjust main heading font-size for mobile */
  h2 {
    font-size: 1.8em;
    margin-bottom: 15px;
  }

  /* Adjust subheadings font-size for mobile */
  h3 {
    font-size: 1.3em;
    margin-top: 20px;
    padding-bottom: 8px;
  }

  /* Reduce the font-size for paragraphs and list items on mobile */
  p, ul li {
    font-size: 1em; /* Slightly smaller text for better readability on mobile */
  }

  /* Adjust button padding and font-size for smaller screens */
  button {
    padding: 8px 15px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  /* Further reduce the padding and text size for very small screens */
  .navbar {
    padding: 10px;
    font-size: 1.1em;
  }

  .article-container {
    padding: 10px;
  }

  h2 {
    font-size: 1.6em;
  }

  h3 {
    font-size: 1.2em;
  }

  p, ul li {
    font-size: 0.9em;
  }

  button {
    padding: 6px 12px;
    font-size: 13px;
  }
}
